import React, { useState } from 'react';
import clsx from 'clsx';
import useStyles from './use-styles';
import { Description3, Header4 } from 'components/typography';
import Link from 'components/link';

import { baseURL } from 'api';

export function CrewCard({ name, bio, photos, url }) {
	const styles = useStyles();
	const [isHover, setHover] = useState(false);
	const [activeCard, setActiveCard] = useState(false);

	console.log('sdf', baseURL);
	const cardToggle = () => {
		setActiveCard(!activeCard);
	};

	const firstPhoto = `${baseURL}/image/${photos && photos[0]}`;
	const secondPhoto = `${baseURL}/image/${photos && photos[1]}`;

	return (
		<div className={styles.crewCard}>
			<div
				className={clsx(styles.cardPhoto, activeCard && styles.cardActive)}
				onMouseEnter={() => setHover(true)}
				onMouseLeave={() => setHover(false)}
				style={{
					backgroundImage: isHover
						? `url(${firstPhoto})`
						: `url(${secondPhoto})`,
				}}
				onClick={cardToggle}
			>
				<div className={styles.cardInfo}>
					<div className={styles.hoverDescription}>
						QA engineer and project lead with more than 6 years of experience.
					</div>
					<p className={styles.textRow}>
						<span className={styles.textRowHeader}>Manager:&nbsp;</span>
						World athletics projects, <a href='#'>Lucky Voice</a>,{' '}
						<a href='#'>Sixley</a>
					</p>
					<p className={styles.textRow}>
						<span className={styles.textRowHeader}>QA:&nbsp;</span>
						<a href='#'>Netcel</a>
					</p>
					<div className={styles.bottom}>
						<Link title={'Explore more'} url={`team/${url}`} white />
					</div>
				</div>
			</div>

			<div className={styles.cardName}>
				<Link title={name} url={'team/mykola'} />
			</div>

			<Description3 title={bio} className={styles.customDescription} />
		</div>
	);
}

export function ProjectCard({ image, icon, description }) {
	const styles = useStyles();
	return (
		<div className={styles.projectCard}>
			<div
				className={clsx(styles.cardPhoto, styles.projectPhoto)}
				style={{
					backgroundImage: `url(${require(`assets/images/lv-img-${image}.png`)})`,
				}}
			></div>

			<div className={styles.cardName}>
				<Header4 title={description} className={styles.customDescription} />
			</div>
			<div
				className={styles.cardLogo}
				style={{
					backgroundImage: `url(${require(`assets/icons/logo-icon-${icon}.png`)})`,
				}}
			></div>
		</div>
	);
}

export function InfoCard({ title, requirements }) {
	const styles = useStyles();

	return (
		<div className={styles.infoCard}>
			<Header4 title={title} />
			<ul className={styles.requirements}>
				{requirements.map((item) => {
					return <li>{item}</li>;
				})}
			</ul>
			<Link title='Learn more' />
		</div>
	);
}
